.sidebar{
    flex: 0.2;
    top: 80px;
    border-radius: 10px;
    text-align: center;
    height: fit-content;
    width: 100%;
    position: sticky;
}

.sidebar__top{
    display: flex;
    flex-direction: column;
    align-items: center; 
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
}

.sidebar__top > h2{
    font-size: 18px;
}

.sidebar__top > h4{
    font-size: 12px;
    color: gray;
}

.sidebar__avatar{
    margin-bottom: 10px;
}

.sidebar__top > img{
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover; 
}

.sidebar__stats{
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid lightgray;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    background-color: white;
}

.sidebar__stat{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.sidebar__stat > p{
    color: gray;
    font-size: 13px;
    font-weight: 600;
}

.sidebar__statNumber{
    color: #2e81f4 !important;
    font-size: 13px;
    font-weight: bold;
}

.sidebar__bottom{
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    border-radius: 10px;
    background-color: white;
    margin-top: 10px;
}

.sidebar__bottom > p{
    font-size: 13px;
    padding-bottom: 10px;
}   

.sidebar__recentItem{

    display: flex;
    font-size: 13px;
    color: gray;
    font-weight: bolder;
    cursor: pointer;
    margin-bottom: 5px;
}

.sidebar__recentItem:hover {
    /* color: #2e81f4;
    transform: scale(1.05);
    transition: transform 0.2s ease-in-out; */
    background-color: whitesmoke;
    border-radius: 10px;
    color: black;
    cursor: pointer;
}

.sidebar__hash{
    margin-right: 10px;
    margin-left: 5px;
}